.navContainer {
  color: var(--navTextColor);
  border-bottom: 2px solid var(--textColor);
  font-size: 1.2rem;
}

header {
  display: grid;
  grid-template-rows: auto auto;
  padding: 0 2vw;
  max-width: var(--maxSize);
}

.navigation {
  grid-column: 1;
  height: 0;
  transition: height 0.5s ease-in-out;
  overflow: hidden;
  visibility: hidden;
  padding: 0 2vw;
  padding-top: 05px;
}

.navigation.show {
  height: 14rem;
  visibility: visible;
  transition: height 0.5s ease-in-out;
}

.logoSection {
  margin-right: 2vw;
  margin-left: 2vw;
  display: grid;
  grid-template-columns: auto auto;
}

.menuButton {
  background-color: transparent;
  font-weight: bold;
  border: none;
  color: #f2f4f3;
  font-size: 1.45rem;
  cursor: pointer;
  border-radius: 5px;
  justify-self: start;
  padding: 2vh 2vw;
}

.menuButton:hover {
  background-color: var(--navTextColor);
}

.mainLogo {
  justify-self: end;
  grid-column: 2;
  display: flex;
  align-items: center;
}

.mainLogo > img {
  max-width: 3rem;
  max-height: 3rem;
}

.mainLogo > div {
  margin-left: min(15px, 2vw);
  font-weight: bold;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.menuLinks {
  margin-top: 10px;
  list-style: none;
  grid-column: 1 2;
  text-align: center;
  position: relative;
}

.menuLinks > li > a {
  display: block;
  padding: 0.8rem 0;
  border-radius: 5px;
  color: var(--textColor);
  background-color: var(--navTextColor);
  font-weight: bold;
  margin-bottom: 10px;
}

.navigationButtonsAnim:hover {
  text-decoration: underline !important;
}

li > .navigationButtonsAnim:hover {
  animation: colorChange 0.3s;
  animation-fill-mode: forwards;
  text-decoration: underline !important;
}

@keyframes colorChange {
  0% {
    color: var(--textColor);
  }

  25% {
    color: #d2dce0;
  }

  50% {
    color: #bbdce9;
  }

  100% {
    color: #83dbfa;
  }
}

@media only screen and (max-width: 520px) {
  .menuLinks > li > a {
    padding: 0.85rem 0;
  }

  .menuLinks {
    font-size: 1.2rem;
  }
}

@media only screen and (min-width: 1000px) {
  .navContainer {
    margin: auto;
  }

  header {
    margin: auto;
  }

  .logoSection {
    grid-row: 1;
    justify-self: start;
    margin-left: 0px;
    margin: 0;
    padding: 0;
  }

  .menuButton {
    display: none;
  }

  .navigation {
    visibility: visible;
    height: auto;
    grid-row: 1;
    grid-column: 2;
    justify-self: end;
    margin: 0;
    padding: 0;
  }

  .menuLinks {
    display: flex;
    margin: auto;
    margin: 1px;
  }

  .menuLinks > li > a {
    display: block;
    padding: 1rem 1.2rem;
    width: auto;
    background-color: transparent;
    font-weight: bold;
  }

  .menuLinks > li:last-child > a {
    padding-right: 0px;
  }
}
