@media only screen and (min-width: 1000px) {
  .articleContainer {
    display: flex;
    flex-direction: row;
    max-width: var(--maxSize);
    margin: auto;
  }

  .articleContainer > main {
    width: 73%;
    align-self: flex-start;
  }

  .articleNav {
    width: 23%;
    margin-left: 4%;
    padding-bottom: 10px;
    padding: 1rem 0rem;
    margin-top: max(15px, 1vh);
    position: sticky;
    top: 0;
    height: 20vh;
  }

  .articleNav > p {
    text-align: center;
    margin-bottom: 1rem;
    margin-right: 1rem;
    border-top: 1px solid white;
    border-bottom: 1px solid white;
    padding-top: 15px;
    padding-bottom: 15px;
    font-weight: bold;
  }

  .articleContents {
    list-style: none;
    padding-left: 5%;
    font-size: 0.95rem;
  }

  .articleContents > li {
    padding: 0.4rem 0px;
  }

  .articleContents > li > a {
    text-decoration: underline !important;
  }

  .articleContents > li > a:focus {
    border-bottom: 1px solid;
    filter: brightness(120%);
  }

  .articleContents > li > a:hover {
    border-bottom: 1px solid;
    filter: brightness(120%);
  }

  .articleContents > li > a:active {
    color: var(--navTextColor) !important;
  }
}
