:root {
  --mainColor: #1e1e1e;
  --textColor: #dcdcdc;
  --headerColor: #dcdcaa;
  --titleColor: #4ec9b0;
  --navTextColor: #3075ca;
  --addInfoColor: #579a37;
  --maxSize: 1300px;
  --maxMainSize: 1000px;
}

:root.changeTheme {
  --mainColor: #dcdcdc;
  --textColor: #1e1e1e;
  --headerColor: #868668;
  --titleColor: #4ec9b0;
  --navTextColor: #3075ca;
}

* {
  margin: 0;
  padding: 0;
}

*:focus:not(:focus-visible) {
  outline: none;
}

*:focus-visible {
  outline: ghostwhite dashed 2px;
}

a {
  text-decoration: none;
  color: inherit;
}

html {
  font-size: calc(15px + 6 * ((100vw - 360px) / 1000));
  scroll-behavior: smooth;
}

body {
  background-color: var(--mainColor);
  color: var(--textColor);
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  line-height: 1.5;
  min-height: 100vh;
}

.siteTitle {
  color: var(--titleColor);
}

main {
  margin: auto;
  padding-bottom: 10px;
  padding-top: max(20px, 2vh);
}

@media only screen and (min-width: 1000px) {
  main {
    max-width: var(--maxMainSize);
  }
}

@media only screen and (min-width: 1200px) {
  html {
    font-size: 20px;
  }
}
